@import '../../../utils/constants.scss';

.navigation {
  position: relative;
  display: flex;
  justify-content: center;
  width: 60%;
  z-index: 2;

  .navigation__buttons {
    @include indent-null();
    width: 100%;
    list-style: none;
    display: flex;
    justify-content: flex-end;
  }

  .navigation__buttons-item {
    width: 33%;
    margin-right: 25px;
    box-shadow: $shadow-border;
    &:last-child {
      margin-right: 10px;
    }
  }

  .navigation__button {
    @include flex-center();
    @include text(15px, 16px, 400);
    border: none;
    text-decoration: none;
    width: 100%;
    height: 100%;
    background-color: white;

    color: $text-color;
    &:focus {
      font-weight: 500;
      opacity: 1;
    }
  }

  .navigation__button_active {
    background-color: rgb(247, 247, 247);
    box-shadow: $inner-shadow;
  }
}
