@import '../../../utils/constants.scss';

.skills {
  @include flex-center();
  flex-direction: column;
  max-width: 950px;
  width: 100%;
  color: $text-color;

  .skills__title {
    @include text(32px, 32px, 500);
    color: rgb(77, 174, 212);
    letter-spacing: -2px;
    margin: 510px 0 70px;
  }

  .skills__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .skills__subtitle {
    max-width: 265px;
    width: 100%;
    color: #2e4661;
    font-weight: 500;
    text-align: center;
  }

  .skills__special-container {
    list-style: none;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 950px;
    width: 100%;
    padding: 0;
  }
}

.skills__additional-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 950px;
  width: 100%;
  padding: 0;
}
